.main {
    font-family: 'Lato', sans-serif;
    color: #2b3120;
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.fof h1 {
    font-size: 50px;
    display: inline-block;
    padding-right: 12px;
    animation: type .5s alternate infinite;
    transition: all 0.6s;
}
@keyframes type {
    from {box-shadow: inset -3px 0 0 #2b3120;}
    to {box-shadow: inset -3px 0 0 transparent;}
}
@media screen and (max-width: 450px){
    .fof h1 {
        margin-top: -30px;
    }
}