.wrapper {
    padding-top: 20px;
    padding-left: 50px;
}
.wrapper h1 {
    background: #2b3120;
    color: #fff;
    padding: 10px 10px 15px 40px;
    border-radius: 20px;
    max-width: 350px;
}
.formContainer {
    background: #d6d6d6;
    max-width: 360px;
    border-radius: 10px;
    padding: 20px 5px 25px 35px;
}
.inputBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 330px;
    padding-bottom: 10px;
}
.email, .password {
    font-weight: 600;
    font-size: 1.1em;
}
.formSummaryError {
    display: inline-block;
    color: darkred;
    border: 2px solid darkred;
}
.rememberBlock {
    display: flex;
    max-width: 102px;
    justify-content: space-between;
    flex-direction: row;
    padding-bottom: 10px;
}
.rememberBlock label {
    font-weight: 600;
}
.boxes {
    cursor: pointer;
    margin-top: 5px;
    width: 16px;
    height: 16px;
    opacity: 0.6;
}
.boxes:checked {
    background: #2b3120;
    color: #2b3120;;
    opacity: 1;
}
.loginBtn {
    cursor: pointer;
    margin-top: 5px;
    width: 120px;
    padding: 8px;
    font-size: 1em;
    letter-spacing: 0.05em;
    background: linear-gradient(to left top, #000, #2b3120) fixed;
    border-radius: 10px;
    border: 2px solid rgba(255,255,255,0.2);
    color: rgba(255,255,255,0.8);
    transition: all 0.5s;
}
.loginBtn:hover {
    border: 2px solid rgba(255,255,255,0.5);
    background: linear-gradient(to left top, #000, #008800) fixed;
}
@media screen and (max-width: 570px) {
    .wrapper {
        padding: 10px 10px 10px 15px;
    }
    .wrapper h1 {
        padding: 10px 10px 15px 20px;
        font-size: 1.3em;
        max-width: 220px;
    }
    .formContainer {
        padding: 10px 10px 15px 20px;
    }
    .inputBlock {
        flex-direction: column;
    }
}



